<template>
  <h1>{{ phrase }}</h1>
</template>

<script>
import Vue from 'vue';
import YamlReader from './components/config/yaml-reader';
import Tokens from './components/config/tokens';
import path from 'path';
import When from './components/constructor/token-providers/when';

let yml = require(`json-loader!yaml-loader!./etc/tokens.yaml`);

//let yml = (new YamlReader()).read(path.resolve(__dirname, 'etc', 'tokens.yaml'));

let tokens = new Tokens(yml);
let when = new When(tokens);

export default {
  data: {
    phrase: ''
  },
  created () {
    this.phrase = when.get('past');
  }
};
</script>

